async function getImages(imgDets, urls, handleImageDetails, startLoad) {
  const urlFileMap = await downloadImages(urls);
  // const urlBase64Map = await convertToBase64(urlFileMap);
  startLoad();
  const tempImages = await Promise.allSettled(
    imgDets.map((img) => {
      const result = new Promise((resolve) => {
        const { naturalWidth, naturalHeight, title, aspectRatio, type } = img;
        const file = urlFileMap[title];
        // const base64SRC = urlBase64Map[title];
        const image = new Image();
        image.onload = (e) => {
          const { src } = e.target;
          const imageDetail = {
            title,
            src,
            naturalHeight,
            naturalWidth,
            aspectRatio,
            type,
            file,
          };
          handleImageDetails(imageDetail);
          resolve(imageDetail);
        };
        image.src = URL.createObjectURL(file);
      });
      return result;
    })
  );
  return tempImages.map((elem) => elem.value);
}

export default function CollageView() {
  const params = useParams();
  const { collageID } = params;

  const getCollage = async (
    handleImageCount,
    handleNewImageCount,
    handleImageDetails
  ) => {
    const details = await collageapi.getCollage(collageID);
    const images = await getImages(
      details.details.images,
      details.urls,
      handleImageDetails,
      () => {
        handleImageCount(details.details.images.length);
        handleNewImageCount(details.details.images.length);
      }
    );

    const imageDets = images.map((img) => {
      const { src, originalSRC, file, ...rest } = img;
      return rest;
    });
    return { details: { ...details.details, images: imageDets }, images };
  };

  return (
    <BaseCollage
      getCollage={getCollage}
      clearCollage={() => collageapi.deleteCollage(collageID)}
      saveType="existing"
    />
  );
}
